<template>
<div id="goldenTeacher" class="golden-teacher">
  <div class="golden">
    <h2 class="golden-title">金牌講師領軍- Bob Hsu 鮑伯老師</h2>
    <p class="golden-subtitle">領受使命為下一代競爭力而打拼,讓學生享受業界專業訓練,建立高度視野,以找到生命的方向與價值</p>
    <div class="golden-description">
      <div class="golden-rotate">
        <p class="golden-rotate-text">席思人工智能學院  總經理</p>
        <p class="golden-rotate-text">AI Hub Taipei平台發起人</p>
        <p class="golden-rotate-text">新北市教育局 AI資訊師培講師</p>
      </div>
      <h3 class="golden-experience">工作經歷</h3>
      <div class="golden-out-box golden-text">
        <div class="golden-box">
          <p>1.美商Synergies 資深AI工程師</p>
          <p>2.天元數位科技 共同創辦人兼執行長</p>
        </div>
        <div>
          <p>3.兩岸眾多龍頭企業系統顧問</p>
          <p>4.兩岸眾多大學與中學AI講師</p>
        </div>
      </div>
      <h3 class="golden-experience">三大獨特教學方法-啟發學員天賦，人人皆能以簡單方式征服程式</h3>
      <div class="golden-text">
        <p><span class="golden-pre-text">1.逆向工程教學法</span>:了解資深工程師思考跟解決問題思維，快速站在巨人的肩膀上，以在短時間內能實作出成品。</p>
        <p><span class="golden-pre-text">2.遊戲生活教學法</span>:學習目標跟生活緊密連結，並且結合遊戲式的引導，讓孩子享受其中又能將概念應用於生活。</p>
        <p><span class="golden-pre-text">3.高原騰飛教學法</span>:市場獨家教學秘訣，解決絕大多數學生高原期放棄問題，讓學員享受於逆境征服向上的喜悅。</p>
      </div>
    </div>
  </div>
  <img class="golden-icon" src="@/assets/bob-icon.png" alt="icon">
</div>
</template>

<script>

export default {
  name: 'ApplySubject'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.golden-teacher {
  background-image: url('../../../assets/green-background.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position-x: center;
  padding: 60px 90px 33px;
  position: relative;

  .golden {

    &-title {
      letter-spacing: 14px;
      color: #f5cb5c;
      text-shadow: 0 2px 4px #5c6b73;
    }

    &-subtitle {
      width: 624px;
      color: $theme-color-dark-black;
      font-size: 18px;
      line-height: 28px;
      letter-spacing: 7.5px;
      margin-bottom: 116px;
    }

    &-rotate {
      width: 624px;
      display: flex;
      flex-direction: column;
      align-items: center;
      position: absolute;
      top: -110px;
      left: 0px;
    }

    &-rotate-text {
      padding: 8px 0px 12px;
      transform: rotate(-3deg);
      background-color: $theme-color-primary;
      text-align: center;
      color: $theme-color-white-blue;
      font-size: 21px;
      font-weight: 600;
      line-height: 33px;
      letter-spacing: 5.25px;

      &:nth-child(1) {
        width: 390px;
      }

      &:nth-child(2) {
        width: 420px;
        position: absolute;
        top: 52px;
      }

      &:nth-child(3) {
        width: 450px;
        position: absolute;
        top: 104px;
      }
    }

    &-description {
      width: 624px;
      position: relative;
      background-color: $theme-color-white;
      padding: 76px 50px 16px 30px;
    }

    &-experience {
      font-size: 18px;
      font-weight: 500;
      letter-spacing: 7.5px;
      color: $theme-color-primary;
    }

    &-out-box {
      display: flex;
      justify-content: space-around;
      margin-bottom: 10px;
    }

    &-box {
      padding-right: 20px;
    }

    &-pre-text {
      color: $theme-color-dark-blue;
    }

    &-text {
      font-size: 12px;
      font-weight: 500;
      line-height: 18px;
      letter-spacing: 1px;
      color: $theme-color-dark-black;
    }

    &-icon {
      position: absolute;
      bottom: 0px;
      right: 90px;
    }

  }

}

@media( max-width: 1350px ) {
  .golden-teacher {

    .golden {

      &-icon {
        width: 350px;
        right: 10px;
      }
    }
  }

}

@media( max-width: 1023px ) {

.golden-teacher {
  padding: 29px 10px 0px;

  .golden {

    &-title {
      font-size: 20px;
      letter-spacing: 8.2px;
      margin-bottom: 16px;
    }

    &-subtitle {
      width: auto;
      font-size: 12px;
      letter-spacing: 5px;
      margin-bottom: 90px;
    }

    &-rotate {
      width: 100%;
      top: -84px;
    }

    &-rotate-text {
      font-size: 14px;
      letter-spacing: 3.5px;

      &:nth-child(1) {
        width: 230px;
      }

      &:nth-child(2) {
        width: 250px;
        top: 41px;
      }

      &:nth-child(3) {
        width: 270px;
        top: 81px;
      }
    }

    &-description {
      width: auto;
      margin-bottom: 20px;
      padding: 70px 22px 16px;
    }

    &-experience {
      font-size: 18px;
      letter-spacing: 7.5px;
    }

    &-out-box {
      flex-direction: column;
    }

    &-box {
      padding: 0px;
    }

    &-box p {
      margin-bottom: 0px;
    }

    &-text {
      font-size: 10px;
      letter-spacing: 4.2px;
    }

    &-icon {
      position: initial;
      width: 80%;
      margin-left: 20%;
      transform: translate(10px, 6px);
    }

  }

}

}

</style>



