<template>
<div class="select-reward">
    <div class="select">
        <h1 class="title">評選與獎項肯定</h1>
        <p class="subtitle">領先業界的技術與產品，榮獲美國權威雜誌、全球最大工業展</p>
        <div class="reward">
            <div class="reward-box">
                <img class="reward-img" src="@/assets/award-fish.png" alt="award-fish">
                <div class="reward-up-text">《紅鯡魚》US Top 100</div>
                <div class="reward-down-text">以獨創的決策AI技術解決企業資料的分析問題</div>
            </div>
            <div class="reward-box">
                <img class="reward-img" src="@/assets/award-mvp.png" alt="award-fish">
                <div class="reward-up-text">《經理人》MVP 100</div>
                <div class="reward-down-text">以新銳新創公司管理者之姿</div>
            </div>
            <div class="reward-box">
                <img class="reward-img" src="@/assets/award-cio.png" alt="award-fish">
                <div class="reward-up-text">《CIO Review》</div>
                <div class="reward-down-text">工業決策自動化的認知系統20大最有前景的智慧系統解決方案供應商</div>
            </div>
            <div class="reward-box">
                <img class="reward-img" src="@/assets/award-robot.png" alt="award-fish">
                <div class="reward-up-text">《Robotics Award》2019 Final 10</div>
                <div class="reward-down-text">德國漢諾威工業展以突破性的資料視覺化方案</div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
  name: 'SelectReward'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.select-reward {

    .reward {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 90px 100px;

        &-box {
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 15px;
        }

        &-img {
            width: auto;
            height: 160px;
        }

        &-up-text {
            font-size: 24px;
            color: $theme-color-dark-black;
            text-align: center;
            padding: 20px 0px 16px;
            height: 80px;
            line-height: initial;
        }

        &-down-text {
            font-size: 16px;
            color: $theme-color-light-gray;
            text-align: center;
            height: 60px;
            line-height: initial;
        }

    }

}

@media( max-width: 1023px ) {

    .select-reward {

        .reward {
            flex-direction: column;
            padding: 18px;

            &-box {

            }

            &-img {

            }

            &-up-text {
                height: auto;
                font-size: 18px;
            }

            &-down-text {
                font-size: 16px;
                line-height: 26px;
                height: auto;
            }
        }

    }

}

</style>



