<template>
<div @click="closeAllFloat" class="apply-subject">
    <h1 class="title">12大應用主題與作品</h1>
    <p class="subtitle">多元生活化專案課程</p>
    <div class="subject">
        <single-subject
        v-for="(content, index) in contents"
        :key="index"
        :content="content"
        ref="singleChild"
        @closeFloat="closeAllFloat"
        >
        </single-subject>
    </div>

</div>
</template>

<script>
import SingleSubject from '@/pages/Student/components/SingleSubject'

export default {
  name: 'ApplySubject',
  components: {
    SingleSubject
  },
  data () {
    return {
      contents: [
        {img: 'subject-ai', title: '基礎課程-AI導論', star: 1, text: '學習數據分析與可視化套件，支援維度陣列與矩陣運算，透過標籤和索引來整理資料格式，用以快速操作及分析資料，並能以美觀的圖表展示。'},
        {img: 'subject-basic', title: '基礎課程-python', star: 1, text: '學習數據分析與可視化套件，支援維度陣列與矩陣運算，透過標籤和索引來整理資料格式，用以快速操作及分析資料，並能以美觀的圖表展示。'},
        {img: 'subject-python', title: 'Python函式庫', star: 2, text: '學習數據分析與可視化套件，支援維度陣列與矩陣運算，透過標籤和索引來整理資料格式，用以快速操作及分析資料，並能以美觀的圖表展示。'},
        {img: 'subject-bike', title: '共享單車需求分析  ', star: 2, text: '使用機器學習演算法，實作迴歸分析模型，利用時間、季節、是否是特別假日、是否是工作日、天氣狀況等資訊，來預測每小時的腳踏車數量。'},
        {img: 'subject-house', title: '房屋價格預測走勢', star: 3, text: '使用深度神經網絡，實作迴歸分析模型，利用坪數、地段、層數、屋齡等房子的相關資訊，來預測該房屋格走勢。'},
        {img: 'subject-messenger', title: '聊天機器人', star: 3, text: '使用python基於DNN對於來的話進行分類，並且檢索出機器人應該應答的內容，做出屬於自己的機器人開始聊天吧！'},
        {img: 'subject-animal', title: '貓狗圖像辨識', star: 3, text: '使用卷積神經網絡，實作二元分類模型，利用圖片當中的特徵，來預測是貓或狗。'},
        {img: 'subject-face', title: '人臉表情分類', star: 4, text: '使用CNN卷積神經網路辨別你的喜、怒、哀、樂等各項特徵，讓的電腦感受到人們表情下的內涵。'},
        {img: 'subject-ship', title: '鐵達尼號生存預測', star: 4, text: '使用機器學習演算法，實作二元分類模型，利用船上乘客資料來預測在船難是否存活。'},
        {img: 'subject-music', title: '音樂風格分析', star: 4, text: '使用卷積神經網路與音樂音檔資料處理技術，讓AI模型能分析出一段音樂所屬風格，諸如爵士、古典、搖滾、鄉村等或以其他分類方式，快速實現風格分類。'},
        {img: 'subject-emotion', title: '情緒分析-電影評論 ', star: 5, text: '使用遞迴神經網絡，實作二元分類模型，利用電影相關的IMDb網路資料庫中逾四百多萬作品資料，涵蓋50,000 筆 "影評文字”，找出作者表達中的評價或情緒。'},
        {img: 'subject-chip', title: '遊戲族群行銷分析', star: 5, text: '用資料科學的方式，將遊戲產業的購買數據，進行大數據分析，以此找出顧客喜好與分群預測。'}
      ]
    }
  },
  methods: {
    closeAllFloat () {
      for (let i = 0; i < 12; i++) {
        this.$refs.singleChild[i].closeFloating()
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.apply-subject {
    padding: 0px 60px 60px;

    .subject {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        margin-top: 60px;
    }

}

@media( max-width: 1023px ) {

    .apply-subject {
        padding: 22px;

        .subject {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top: 30px;
        }
    }

}

</style>



