<template>
<div id="studentSchool" class="sis-school">
    <h1 class="title">三大系列課程</h1>
    <div class="sis-box">
      <p class="subtitle">完成難度2：具自行打造機器學習模型能力</p>
      <p class="subtitle">完成難度3：具完成 科展 / 競賽 / 小論文 能力</p>
      <p class="subtitle">完成難度5：具備初階工程師 / 資料科學家水準</p>
    </div>
    <div class="school">
      <div class="school-left">

        <div class="school-title">
          <img class="school-img" src="@/assets/school-lamp.png" alt="lamp">
          <div>學期課程、營隊課程</div>
        </div>
        <div class="school-left-box">
          <div class="school-left-title">
            <p class="school-left-title-inner school-left-title-general">AI概念與通式班</p>
            <P class="school-left-title-inner school-left-title-improve">AI程式進階養成班</P>
          </div>
          <div class="school-left-main-text">
            <div class="school-left-inner-box">
              <div>
                <p>時程：12hr</p>
                <div class="school-difficult">
                  <p>難度：</p>
                  <img class="school-star" src="@/assets/star.png" alt="star">
                </div>
                <p>課程作品：</p>
                <p>1.AI導論</p>
                <P>2.Python程式語言</P>
              </div>
            </div>
            <div class="school-left-inner-box school-deep-background">
              <div>
                <p>時程：24hr</p>
                <div class="school-difficult">
                  <p>難度：</p>
                  <img class="school-star" src="@/assets/star.png" alt="star">
                  <img class="school-star" src="@/assets/star.png" alt="star">
                </div>
                <p>課程作品：</p>
                <p>1.Python 函示庫</p>
                <P>2.共享單車需求分析</P>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="school-right">
        <div class="school-title">
          <img class="school-img" src="@/assets/school-group.png" alt="lamp">
          <div>社團課程、競賽培訓</div>
        </div>
        <div class="school-right-box">
          <div class="school-right-title">AI程式競賽菁英培訓班</div>
          <div class="school-right-main-text">
            <div class="school-right-inner-box">
              <div>
                <p>時程：36hr</p>
                <div class="school-difficult">
                  <p>難度：</p>
                  <img class="school-star" src="@/assets/star.png" alt="star">
                  <img class="school-star" src="@/assets/star.png" alt="star">
                  <img class="school-star" src="@/assets/star.png" alt="star">
                </div>
                <p>課程作品：</p>
                <p>1.房屋價格預測走勢</p>
                <p>2.聊天機器人</p>
                <p>3.貓狗圖像辨識</p>
              </div>
            </div>
            <div class="school-right-inner-box school-deep-background">
              <div>
                <p>時程：36hr</p>
                <div class="school-difficult">
                  <p>難度：</p>
                  <img class="school-star" src="@/assets/star.png" alt="star">
                  <img class="school-star" src="@/assets/star.png" alt="star">
                  <img class="school-star" src="@/assets/star.png" alt="star">
                  <img class="school-star" src="@/assets/star.png" alt="star">
                </div>
                <p>課程作品：</p>
                <p>1.鐵達尼號生存預測</p>
                <p>2.人臉表情分類</p>
                <p>3.音樂風格分析</p>
              </div>
            </div>
            <div class="school-right-inner-box-last">
              <p>時程：36hr</p>
              <div class="school-difficult">
                <p>難度：</p>
                <img class="school-star" src="@/assets/star.png" alt="star">
                <img class="school-star" src="@/assets/star.png" alt="star">
                <img class="school-star" src="@/assets/star.png" alt="star">
                <img class="school-star" src="@/assets/star.png" alt="star">
                <img class="school-star" src="@/assets/star.png" alt="star">
              </div>
              <div class="school-right-last-content-box">
                <div class="school-right-last-content">
                  <p>課程作品：</p>
                  <p>1.情緒分析電影評論</p>
                  <p>2.遊戲族群行銷分析</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</div>
</template>

<script>
export default {
  name: 'SisSchool'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.sis-school {
  padding: 0px 90px 50px;

  .sis-box {
    width: 420px;
    margin: auto;
  }

  .sis-box p {
    width: 420px;
    font-size: 20px;
    text-align: left;
  }

  .school {
    display: flex;
    padding: 90px 0px;

    &-left {
      width: 40%;
    }

    &-title {
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 30px;
      text-align: center;
      color: $theme-color-dark-black;
      margin-bottom: 12px;
    }

    &-img {
      height: 40px;
      margin-right: 5px;
    }

    &-left-title {
      display: flex;
      color: $theme-color-white;
      text-align: center;
      font-size: 18px;
    }

    &-left-title-inner {
      width: 50%;
      padding: 10px 0px;
      margin: 0px;
    }

    &-left-title-general {
      background-color: $theme-color-general-primary;
    }

    &-left-title-improve {

      background-color: $theme-color-light-dark-primary;
    }

    &-left-main-text {
      display: flex;
      font-size: 18px;
    }

    &-left-inner-box {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 50%;
      border: 1px solid rgba(244, 244, 248, 0.8);
      box-shadow: 1px 1px 9px 0 #f1f7f9;
      padding: 17px 8px;
      height: 262px;
    }

    &-difficult {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
    }

    &-star {
      margin-right: 3px;
    }

    &-deep-background {
       background-color: rgba(244, 244, 248, 0.3);
    }

    &-right {
      width: 59%;
    }

    &-right-box {

    }

    &-right-title {
      color: $theme-color-white;
      text-align: center;
      padding: 10px 0px;
      margin: 0px;
      background-color: $theme-color-dark-primary;
      font-size: 18px;
    }

    &-right-main-text {
      display: flex;
      font-size: 18px;
    }

    &-right-inner-box {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 33.3%;
      border: 1px solid rgba(244, 244, 248, 0.8);
      box-shadow: 1px 1px 9px 0 #f1f7f9;
      padding: 17px 8px;
      height: 262px;
    }

    &-right-last-content-box {
      height: 258px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }

    &-right-inner-box-last {
      width: 33.3%;
      border: 1px solid rgba(244, 244, 248, 0.8);
      box-shadow: 1px 1px 9px 0 #f1f7f9;
      padding: 17px 8px;
      height: 262px;
    }
  }

}

@media( max-width: 1233px ) {
  .sis-school {

    .school {

      &-right-last-content-box {
          height: 233px;
      }
    }
  }
}

@media( max-width: 1080px ) {
  .sis-school {

    .sis-box {
      width: 280px;
    }

    .sis-box p {
      width: 280px;
    }

    .school {

      &-left-inner-box {
        height: 300px;
      }

      &-right-inner-box {

        height: 300px;
      }

      &-right-last-content-box {
        height: 300px;
      }

      &-right-inner-box-last {

        height: 300px;
      }

    }
  }
}

@media( max-width: 1023px ) {
  .sis-school {
    padding: 20px 0px 0px;

    .school {
      flex-direction: column;
      padding: 10px 0px 25px;

      &-left {
        width: 100%;
        margin-bottom: 24px;
      }

      &-title {
        font-size: 18px;
      }

      &-img {
        width: 30px;
        height: auto;
      }

      &-left-title {
        font-size: 16px;
      }

      &-left-title-inner {

      }

      &-left-title-general {

      }

      &-left-title-improve {

      }

      &-left-main-text {
        font-size: 14px;
      }

      &-left-inner-box {
        padding: 15px;
        height: 185px;
      }

      &-deep-background {

      }

      &-right {
        width: 100%;
      }

      &-right-box {

      }

      &-right-title {
        font-size: 16px;
      }

      &-right-main-text {
        flex-wrap: wrap;
        font-size: 14px;
      }

      &-right-inner-box {
        padding: 15px;
        height: 235px;
        width: 50%;
      }

      &-right-inner-box-last {
        padding: 15px;
        width: 100%;
        height: auto;
      }

      &-right-last-content-box {
        display: flex;
        flex-direction: row;
        height: auto;
      }

      &-right-last-content {
        width: 100%;
      }
    }
  }
}

</style>



