<template>
<div class="school-feature">
    <div id="commonFeature" class="feature">
        <h1 class="title">學院服務</h1>
        <div class="feature-out-box">
            <div class="feature-box">
                <div class="feature-title">
                    <img class="feature-img" src="@/assets/feature-house.png" alt="feature-house">
                    <div>升學管道</div>
                </div>
                <div class="feature-text">
                    APCS訓練：<br>
                    為教育部所推動檢驗高中生程式設計能力的檢定，國內眾多頂尖大專院校開放的特殊入學管道。訓練立定讀資工相關科系學生，取得APCS認證獲得額外入學機會。<br><br>
                    豐富學習歷程：<br>
                    多樣化的案例教學，讓學員除了理解Python背後的程式邏輯，還能做出大學或業界人士水準成品。不僅僅習得程式語言，也將真正的AI思維一併帶走。<br><br>
                    AI競賽輔導：<br>
                    安排課後輔導，增強學生實作力，並可參與科展、競賽、小論文等，以豐富學習歷程，甚至獲得特殊頂尖大學入學機會。<br><br>
                    留學顧問指導： <br>
                    專業留學顧問協助生涯輔導、申請建議、院校評估，實習與志工媒合等客製化服務。<br><br>
                    專業證書：<br>
                    由鴻海、思科指定合作AI夥伴的美商Synergies與SIS AI College聯合認證證書。<br><br>
                </div>
            </div>
            <div class="feature-box">
                <div class="feature-title">
                    <img class="feature-img" src="@/assets/feature-person.png" alt="feature-person">
                    <div>校園協作</div>
                </div>
                <div class="feature-text">
                    AI科技社團營隊：<br>
                    安排課後社團及營隊活動，讓學生在課餘時間也能接觸AI實作與Python程式語言。<br><br>
                    客製化配套課程：<br>
                    搭配108課綱政策，參考美國CSTA規範，落地為台灣學生與該校特色量身打造課程菜單，配合學生能力及學習速度因材施教。<br><br>
                    進階師資培訓： <br>
                    配合教育局與各校需要，讓學校老師們也能帶領學生進入AI實作、應用與Python語言。<br><br>
                    搭建AI實驗室：<br>
                    幫助學校軟硬體升級，打造AI教學環境，讓學生能在專業的AI實驗室做最有效率的學習。
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
  name: 'SchoolFeature'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.school-feature {

    .feature {

        &-title {
            font-size: 26px;
            font-weight: 500;
            text-align: center;
        }

        &-out-box {
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 30px 40px 90px;
        }

        &-box {
            display: flex;
            align-items: center;
            width: 70%;
        }

        &-img {
            width: 78px;
            margin: 0px 50px;
        }

        &-text {
            margin: 0px 25px;
            font-size: 18px;
        }

    }

}

@media( max-width: 1023px ) {

    .school-feature {

        .feature {

            &-title {
                font-size: 24px;
                margin: 30px 50px;
            }

            &-out-box {
                padding: 10px 10px 40px;
            }

            &-box {
                flex-direction: column;
                width: auto;
            }

            &-img {

            }

            &-text {

            }

        }

    }

}

</style>



