<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.world-consultant {

    .consultant {
        padding: 60px 120px 45px;

        &-row {
            display: flex;
            justify-content: space-evenly;
            padding-bottom: 60px;
        }

        &-out-box {
            display: flex;
            align-items: center;
        }

        &-img {
            width: 180px;
            height: auto;
        }

        &-box {
            width: 270px;
        }

        &-name {
            font-size: 18px;
            padding: 18px;
            color: $theme-color-white;
            background-color: $theme-color-dark-primary;
            border-top-right-radius: 6px;
        }

        &-experience {
            font-size: 16px;
            padding: 18px 0px 0px 18px;
            color: $theme-color-light-gray;
        }
    }

}

@media( max-width: 1370px ) {

    .world-consultant {

        .consultant {

            &-img {
                width: 120px;
            }

            &-box {
                width: 210px;
            }

        }
    }

}

@media( max-width: 1023px ) {

    .world-consultant {

        .consultant {
            padding: 24px;

            &-row {
                padding-bottom: 0px;
                flex-direction: column;
            }

            &-out-box {
                justify-content: center;
                padding-bottom: 18px;
            }

            &-img {

            }

            &-box {

            }

            &-name {
                font-size: 16px;
                padding: 4px;
            }

            &-experience {
                font-size: 14px;
                padding: 8px;
            }
        }

    }
}

</style>



