<template>
<div id="studentFeeback" class="student-feeback">
    <h1 class="title">學生反饋</h1>
    <div class="feeback">
      <div class="feeback-out-box">
        <div class="feeback-box">
          <img class="feeback-img" src="@/assets/male.png" alt="png">
          <div class="feeback-intro">
            <p class="feeback-title">北科大電子系 | 沈同學</p>
            <p class="feeback-title">受訓後成為席思學院講師</p>
          </div>
        </div>
        <p class="feeback-text">我過去已經會寫Python，但還沒接觸過深度學習。我回想課程中最珍貴也是成長最快是在企業實作專案的時候，把過去不熟的概念跟編程技巧都給融會貫通。結業後拿到園區的工作，然而我熱愛與人分享，看到團隊如此有理念，並用許多創新有效的教學方法，讓我決定接受教師培訓，去造福更多學生。</p>
      </div>
      <div class="feeback-out-box">
        <div class="feeback-box">
          <img class="feeback-img" src="@/assets/male.png" alt="png">
          <div class="feeback-intro">
            <p class="feeback-title">東山高中畢業 | 徐同學</p>
            <p class="feeback-title">受訓後錄取台科大工程學士班</p>
          </div>
        </div>
        <p class="feeback-text">老師課堂上概念跟程式講解都很清楚，也讓我發現學AI並沒有那麼困難，在大學後我想對AI有更深的探索。</p>
      </div>
      <div class="feeback-out-box">
        <div class="feeback-box">
          <img class="feeback-img" src="@/assets/male.png" alt="png">
          <div class="feeback-intro">
            <p class="feeback-title">中央經濟系 | 陳同學 </p>
            <p class="feeback-title">受訓後錄取外商AI公司工程部實習</p>
          </div>
        </div>
        <p class="feeback-text">以前學程式很快就想放棄，但席思團隊的教材很專業，讓我用最快的速度去了解工程師怎麼思考。老師不只給魚更教我釣魚，以致於還沒學多久，我就已經能夠去讀高深的技術文件，並且學會工程師最需要具備的debug技能，這是我上那麼多課中，第一次遇到如此紮實又好入門的程式學習方式。</p>
      </div>
      <div class="feeback-out-box">
        <div class="feeback-box">
          <img class="feeback-img" src="@/assets/female.png" alt="png">
          <div class="feeback-intro">
            <p class="feeback-title">金華國中 | 李同學</p>
            <p class="feeback-title"></p>
          </div>
        </div>
        <p class="feeback-text">老師帶的遊戲超好玩!!!助教姐姐也很厲害，什麼問題都能解決，營隊完帶著作品去很有成就感，我希望明年暑假可以再來玩!</p>
      </div>
      <div class="feeback-out-box">
        <div class="feeback-box">
          <img class="feeback-img" src="@/assets/female.png" alt="png">
          <div class="feeback-intro">
            <p class="feeback-title">政大英文系 | 林同學</p>
            <p class="feeback-title">就學中</p>
          </div>
        </div>
        <p class="feeback-text">本以為需要高深的數理背景，沒想到Bob老師能將困難的原理講的如此淺顯易懂，更重要的是發現原來以後各行各業都要用到AI，很慶幸自己能提早了解跟預備。</p>
      </div>
      <div class="feeback-out-box">
        <div class="feeback-box">
          <img class="feeback-img" src="@/assets/male.png" alt="png">
          <div class="feeback-intro">
            <p class="feeback-title">東山高中畢業 | 陳同學</p>
            <p class="feeback-title">受訓後錄取成大資源工程學系</p>
          </div>
        </div>
        <p class="feeback-text">我是在考完學測後跟同學一起來上的，沒想到短短幾堂課，就能把Python學起來，並且在期末作出成品，也在學測二階面試的時候獲得教授肯定，讓我覺得很開心。</p>
      </div>
    </div>
</div>
</template>

<script>
export default {
  name: 'StudentFeeback'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.student-feeback {
  padding: 90px 75px;
  background-color: $theme-color-white-blue;

  .feeback {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 30px 0px;

    &-out-box {
      width: 410px;
      padding: 32px;
      margin: 15px;
      background-color: $theme-color-white;
    }

    &-box {
      display: flex;
      align-items: center;
    }

    &-img {
      width: auto;
      height: 48px;
      margin-right: 24px;
    }

    &intro {

    }

    &-title {
      text-align: left;
      color: $theme-color-dark-gray;
      font-size: 18px;
    }

    &-text {
      padding-top: 30px;
      color: $theme-color-dark-gray;
      font-size: 18px;
    }
  }
}

@media( max-width: 1023px ) {

.student-feeback {
  padding: 25px 25px 10px;

  .feeback {

    &-out-box {
      max-width: 300px;
      width: 90%;
      padding: 22px 10px 10px;
    }

    &-box {
      justify-content: center;
    }

    &-img {

    }

    &intro {

    }

    &-title {
      font-size: 14px;
    }

    &-text {
      padding-top: 0px;
      font-size: 14px;
    }
  }
}
}

</style>



